import {common} from "../../utils/api/url";

import mixins from "~/utils/mixins";
import mixinsBridge from "~/utils/mixinsBridge";
export default {
  name: 'appIndex',
  layout: 'defaultAppScreen',
  mixins: [mixins,mixinsBridge],
  data(){
    return {
      active: 6,
      topHeight: 0,
      serverAppList: [],
      noticeAppList: [],
      pageType: '',
    }
  },
  mounted() {

  },
  created() {
    this.active = this.$route.query.activeType ? this.$route.query.activeType : 6;
    this.topHeight = this.navHeight > 0 ? (parseInt(0) + parseInt(this.navHeight)) : 10;

    if (this.$route.query.sessionId){
      this.initAppConfig();
    }else{
      this.pageType = this.$route.query.type ? this.$route.query.type : 'server';
      if (process.browser) {
        let pageType = localStorage.getItem("pageType");
        localStorage.setItem("pageType", this.pageType);
      }

      if (this.active == 6){
        this.initAppRecommend();
      }else {
        this.initAppServer();
      }
    }
  },
  methods: {
    layoutInit(){

    },
    async initAppConfig(){
      await this.autoLoginApp();
      //await this.getSessionInfo();
      setTimeout(()=>{
        if (this.active == 6){
          this.initAppRecommend();
        }else {
          this.initAppServer();
        }
      },1000);
    },
    initAppRecommend(){
      let params = {};
      this.$axios.get(common.server_list_list3, {params: params}).then(res => {
        if (res.data.data){
          this.serverAppList = res.data.data;
        }
      });
    },
    initAppServer(){
      let params = {
        appletType: this.active,
      };
      this.$axios.get(common.server_list_list, {params: params}).then(res => {
        if (res.data.data){
          this.serverAppList = res.data.data;
        }
      });
    },
    serverBlock(event, item){
      if (item == 'all'){
        this.$router.push({
          path: '/app/appAllServer',
          query: {
            activeType: this.active,
            userType: this.loginUserAppType,
            navH: this.navHeight,
            appType: this.globalAppShow,
            sessionId: this.$route.query.sessionId
          }
        });
      }else {
        // this.$router.push({
        //   path: '/app/appServer',
        //   query: {
        //     id: item.id,
        //     activeType: this.active,
        //     userType: this.loginUserAppType,
        //     navH: this.navHeight,
        //     appType: this.globalAppShow
        //   }
        // });
        this.$router.push({
          path: '/app/appServerForm',
          query: {
            id: item.id,
            activeType: this.active,
            userType: this.loginUserAppType,
            navH: this.navHeight,
            appType: this.globalAppShow,
          sessionId: this.$route.query.sessionId,
            page: '/app/appIndex'
          }
        });
      }
    },
    activeTabMenu(name){
      this.noticeAppList = [];
      this.serverAppList = [];
      if (name == 5) {
        this.$router.push({
          path: '/app/appMyNotice',
          query: {
            id: parseInt(name),
            userType: this.loginUserAppType,
            navH: this.navHeight,
            appType: this.globalAppShow,
            sessionId: this.$route.query.sessionId
          }
        });
      // }else if (name == 6){
      //   this.active = name;
      //   this.initAppRecommend();
      // }else {
      //   this.active = name;
      //   this.initAppServer();
      }else if(name == 6){
        this.$router.push({
          path: '/app/appStatic',
          query: {
            id: parseInt(name),
            userType: this.loginUserAppType,
            navH: this.navHeight,
            appType: this.globalAppShow,
            sessionId: this.$route.query.sessionId
          }
        });
      }else {
        this.active = name;
        this.$router.push({
          path: '/app/appAllServer',
          query: {
            activeType: this.active,
            userType: this.loginUserAppType,
            navH: this.navHeight,
            appType: this.globalAppShow,
            sessionId: this.$route.query.sessionId
          }
        });
      }
    },
    returnIndex(){
      this.returnGlobalMain(1);
    }
  }
}
